* {
	padding: 0;
	margin: 0;
	border: 0;
 }
 *, *:before, *:after {
	box-sizing: border-box;
 }
 :focus, :active {
	outline: none;
 }
 a:focus, a:active {
	outline: none;
 }
 nav, footer, header, aside {
	display: block;
 }
 html, body {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
 }
 input, button, textarea {
	font-family: inherit;
	font-size: inherit;
	color: inherit;
	background-color: transparent;
 }
 input::-ms-clear {
	display: none;
 }
 button {
	cursor: pointer;
 }
 button::-moz-focus-inner {
	padding: 0;
	border: 0;
 }
 a {
	color: inherit;
	text-decoration: unset;
 }
 a, a:visited {
	text-decoration: none;
 }
 a:hover {
	text-decoration: none;
 }
 ul li {
	list-style: none;
 }
 img {
	vertical-align: top;
 }
 h1, h2, h3, h4, h5, h6 {
	font-size: inherit;
	font-weight: 400;
 }
 
/*? Vars */

:root {
	--color-primary: #181820;
	--color-secondary-1: #21212B;
	--color-secondary-2: #272732;
	--color-secondary-3: #33333f;
	--color-pink: #FB76A1;
	--color-red-1: #CC3333;
	--color-red-2: #CC0605;
	--color-gray-1: #BCBCBF;
	--color-gray-2: #5E5E5E;
	--color-white: #ffffff;
}
/*? Global */



h1 {
	font-size: 28px;
	font-weight: 500;
}

body {
	font-family: 'Roboto', sans-serif;
	font-size: 16px;
	font-weight: 400;
	color: var(--color-white);

	min-height: 100%;
	overflow: hidden;

	background-color: var(--color-primary);
}

.container {
	max-width: 532px;
	padding: 0 16px;
	margin: 0 auto;
}

*, *::before, *::after {
	transition: all .4s ease;
}
