/*? Home */

.home {
	height: 100vh;
	overflow-y: hidden;
}

.container {
	max-height: 100%;

	padding-top: 40px;
	padding-bottom: 20px;

	display: grid;
	grid-template-rows: auto 1fr auto;
}

.home__title {
	margin-bottom: 16px;
}

.home__list {
	/* height: 100%; */

	padding: 14px 0;
	margin-bottom: 16px;

	overflow-y: auto;
}


.home__item:not(:last-of-type) {
	margin-bottom: 14px;
}